const SelectFieldOptions = ({ editedField, fieldMethods }: any) => {
  const items = editedField.options.map((e: any) => {
    return (
      <div key={e.id}>
        <input type="text" onChange={() => fieldMethods.onOptionChange(e.id, event)} defaultValue={editedField.o.value} />
        <button id={e.id} onClick={fieldMethods.onOptionDelete}>
          X
        </button>
      </div>
    );
  });
  return <div>{items}<button onClick={fieldMethods.handleOptionSpawn}>New</button></div>;
};
export default SelectFieldOptions;
